import { Link } from "gatsby";
import React, { useEffect } from "react";
import {
  Collapse,
  Nav,
  Navbar,
  NavbarToggler,
  NavItem,
  NavLink,
} from "reactstrap";
import "./BaseLayout.scss";

export default function BaseLayout(
  props: React.PropsWithChildren<{ title: string }>
) {
  const [collapsed, setCollapsed] = React.useState(() => false);
  useEffect(() => {
    document.title = props.title;
  }, [props.title]);

  return (
    <div>
      <Navbar color="dark" expand="md" dark>
        <NavbarToggler
          onClick={function noRefCheck() {
            setCollapsed(!collapsed);
          }}
        />
        <Collapse navbar isOpen={collapsed}>
          <Nav navbar>
            <NavItem>
              <NavLink
                tag={Link}
                to="/corona"
                onClick={() => setCollapsed(false)}
              >
                Corona-Abstandsrechner
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                tag={Link}
                to="/"
                onClick={() => setCollapsed(false)}
              >
                Finalrechner
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                tag={Link}
                to="/kreuze"
                onClick={() => setCollapsed(false)}
              >
                Vorrunden-Wahrscheinlichkeiten
              </NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
      {props.children}
      <div className="text-center">
        &copy; <a href="https://www.hanno-fellmann.de/">Hanno Fellmann</a>{" "}
        &middot;{" "}
        <a href="https://www.hanno-fellmann.de/impressum.html">Impressum</a>
      </div>
    </div>
  );
}
